.cardContainer {
   position: absolute;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   color: white;
   height: 45.5vw;
   width: 50vw;
   left: 49.2vw;
   border: 1px solid black;
   border-radius: 1vw;
   background-color:#EAEAEA;
   overflow-y: scroll;
   padding-bottom: 1vw;
}

.cardContainerBlock {
   display: flex;
}

.cardDataButtons {
   display: flex;
   flex-direction: column;
   align-items: center;
   margin-top: 1vw;
   margin-left: 1vw;
   margin-bottom: 1vw;
   height: 34.3vw;
   width: 22.5vw;
   border: 1px solid black;
   border-radius: 0.5vw;
   z-index: 2;
}

.cardDataButtonsContainer {
   display: flex;
   width: 22vw;
   padding-top: 1vw;
   flex-direction: column;
   align-items: center;
   border-bottom: 1px solid black;
}

.backButton {
   width: 100%;
}

.cardDataButtons .backButton button {
   border: none;
   height: 3vw;
   width: 3vw;
   font-size: 2vw;
   font-family: sans-serif;
   background-color: lavenderblush;
   color: gray;
   margin: 0;
   margin-left: 1vw;
   background: none;
}

.cardDataButtons h1 {
   width: 16vw;
   font-size: 1vw;
   display: flex;
   background: lavenderblush;
   flex-direction: column;
}

.changeFileButton {
   display: flex;
   align-items: center;
   justify-content: center;
   font-family: sans-serif;
   height: 2vw;
   width: 10vw;
   font-size: 1vw; 
   color: black;
   border-radius: 0.5vw;
   border: 1px solid black;
   cursor: pointer;
}

.showFileTitle {
   display: flex;
   align-items: center;
   width: 15vw;
   font-size: 1vw;
   white-space: nowrap; 
   overflow: hidden; 
   text-overflow: ellipsis; 
}

.cardDataButtons button {
   height: 2vw;
   width: 10vw;
   font-size: 1vw;
   margin-bottom: 0.5vw;
   border-radius: 0.5vw;
   border: 1px solid black;
   cursor: pointer;
   background-color: aliceblue;
}

.saveCangesButton {
   position: absolute;
   height: 2vw;
   width: 10vw;
   top: 33.4vw;
   left: 39vw;
   font-size: 1vw;
   border: 1px solid black;
   border-radius: 0.5vw;
   cursor: pointer;
   color: white;
   background-color: #004884;
}

.saveCangesButton:hover {
   background-color: #023a68;
}

.cardDataButtons .deleteCard {
   background-color: #b00303;
   color: white;
   cursor: pointer;
}

.deleteCard:hover {
   background-color: #8b0606;
}

.additionList {
   width: 100%;
   height: 17vw;
   display: flex;
   align-items: center;
   flex-direction: column;
   overflow-x: scroll;
   padding-bottom: 0.5vw;
}

.additionList h1 {
   display: flex;
   justify-content: center;
}

.additionBlockContainer {
   display: flex;
   width: 100%;
   flex-direction: column;
   border: 1px solid black;
   border-radius: 0.5vw;
   margin-top: 0.5vw;
   margin-left: 1vw;
   margin-right: 1vw;
   padding-top: 1vw;
   padding-bottom: 0.5vw;
}

.additionBlockContainer h1 {
   font-size: 0.8vw;
    margin: 0;
    margin-left: 1vw;
    margin-bottom: 0.5vw;
}

.additionBlockContainer:hover {
   background-color: #023a68;
}

.cardDataContainer {
   height: 34.3vw;
   width: 24.5vw;
   border: 1px solid black;
   border-radius: 0.5vw;
   margin: 1vw;
   z-index: 2;
   background-color: #EAEAEA;
}

.cardDataTitle {
   display: flex;
   align-items: center;
   margin-bottom: 1vw;
}

.cardDataContainer h1 {
   width: 8vw;
   color: black;
   display: flex;
   align-items: center;
   font-size: 0.95vw;
   margin-left: 1vw;
   margin-right: 1vw;
}

.cardDataContainer input {
   height: 2vw;
    width: 13.4vw;
    font-size: 1vw;
    background: none;
    border: 1px solid black;
    border-radius: 0.5vw;
    color: black;
    background-color: aliceblue;
}

.cardDataContainer select {
   height: 2vw;
    width: 13.86vw;
    font-size: 1vw;
    background: none;
    border: 1px solid black;
    border-radius: 0.5vw;
    color: black;
    background-color: aliceblue;
}

.cardDataTitleContent {
   height: 9vw;
   display: block;
   align-items: center;
   border: 1px solid black;
   border-radius: 0.5vw;
   padding-bottom: 0.5vw;
   margin-left: 1vw;
   margin-right: 1vw;
   margin-bottom: 0;
}

.cardDataTitleContent h1 {
   color: black;
   font-size: 1vw;
   margin-left: 1vw;
}

.cardDataTitleContent textarea {
   font-size: 1vw;
   overflow-y: auto;
   color: black;
   height: 5.2vw;
   width: 43.5vw;
   margin-left: 1vw;
   margin-right: 1vw;
   resize: none;
   border: 1px solid black;
   padding-left: 1vw;
   padding-right: 1vw;
   border-radius: 0.5vw;
   background-color: aliceblue;
}

.cardPdfDocContainer {
   z-index: 2;
   margin-top: 1vw;
   margin-right: 1vw;
   margin-left: 1vw;
   border: 1px solid black;
   border-radius: 0.5vw;
   min-height: 44.4vw;
   width: 47.8vw;
   background-color: aliceblue;
}
