.cardFilterListContainer {
   position: absolute;
   left: 25vw;
   display: flex;
   width: 23vw;
   align-items: center;
   flex-direction: column;
   height: 46.001vw;
   border: 1px solid black;
   border-radius: 1vw;
   padding-bottom: 0.5vw;
   background-color: #EAEAEA;
}

.filterContainer {
   position: absolute;
   display: flex;
   flex-direction: column;
   align-items: center;
   color: black;
   height: 46.5vw;
   width: 23vw;
   left: 0.88vw;
   border: 1px solid black;
   background-color: #EAEAEA;
   border-radius: 1vw;
}

.filterTitle {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: space-between;
   width: 22vw;
}

.filterTitle button {
   color: black;
   font-size: 2vw;
   font-family: sans-serif;
   height: 3vw;
   width: 3vw;
   display: flex;
   align-items: center;
   justify-content: center;
   border: none;
   background: none;
   cursor: pointer;
}

.filterTitle button img {
   height: 2vw;
   width: 2vw;
}

.filterTitle h1 {
   font-size: 1.5vw;
   margin-top: 1.635vw;
   margin-bottom: 1.635vw;
}

.filterContainer input {
   width: 13vw;
   height: 2vw;
   border-radius: 0.5vw;
   font-size: 1vw;
   background: none;
   border: 1px solid gray;
   color: white;
}

.filterCardFormInput {
   display: flex;
   width: 23vw;
   height: 41.5vw;
   align-items: center;
   flex-direction: column;
   overflow-y: scroll;
   border-top: 1px solid black;
   border-bottom: 1px solid black;
}

.filterErrorTitle {
   font-size: 1vw;
}

.inputsContainer {
   display: flex;
   width: 23.3vw;
   flex-direction: column;
   margin-right: 0vw;
}

.addInput {
   height: 3vw;
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding-left: 1vw;
   margin-bottom: 1vw;
}

.addInput h1 {
   font-size: 1vw;
   width: 9vw;
   margin: 0;
}

.addInput select {
   height: 2vw;
   width: 13.4vw;
   font-size: 1vw;
   background: none;
   border: 1px solid gray;
   border-radius: 0.5vw;
   color: white;
}

.addInput input {
   width: 13vw;
   height: 2vw;
   border-radius: 0.5vw;
   font-size: 1vw;
   background: none;
   border: 1px solid gray;
   color: white;
}

.filterButtons {
   margin-top: 0.5vw;
   width: 21vw;
   display: flex;
   justify-content: end;
}


.filterButtons button {
   background-color: #004884;
   color: white;
   border: 1px solid gray;
   border-radius: 0.5vw;
   height: 2vw;
   width: 10vw;
   font-size: 1vw;
   cursor: pointer;
}
.filterButtons button:hover {
   background-color: #023a68;
}
